            transform: translate(calc(3.2rem - 50vw), calc(60vh - 20px));
    background: #FA7EA8;
    background: var(--pink-3);
  }
  5% {
    -webkit-transform: translate(calc(3rem - 50vw), calc(60vh - 20px));
            transform: translate(calc(3rem - 50vw), calc(60vh - 20px));
  }
  40% {
    -webkit-transform: translate(20vw, calc(60vh - 20px));
            transform: translate(20vw, calc(60vh - 20px));
  }
  60% {
    -webkit-transform: translate(20vw, 0);
            transform: translate(20vw, 0);
    background: #EB5286;
    background: var(--pink-1);
  }
}

